import React from "react";
import "./Hellomsj2.css";
import DataContext from "./DataContext";

class ToDoApp extends React.Component{
    
    constructor(props){
        super(props);

        this.state = {
            text: " ",
            items:[],
            items2:[]
        }
        
        this.handleChange = this.handleChange.bind(this);
        this.handleClick = this.handleClick.bind(this);
    }

    static contextType = DataContext;

    handleClick(){
        
        if(this.state.text.lenght===0)
        return;

        let newitem = {
            id:Date.now(),
            text: this.state.text,
        }    
        console.log(newitem);

        this.setState((state) => ({
            items: state.items.concat(newitem), //forma1
            items2:[...state.items, newitem], //forma2
            text: ""
        }));
    }

    handleChange(event){
        this.setState( ()=>({
           text: event.target.value
           }
        ));
    }

    render(){
        const producto = this.context;
        
        return <>
            <h1>Tareas Pendientes</h1>
            <pre>Del Usuario: {JSON.stringify(this.props.user, null, 2)}</pre>
            <label>Cosas por hacer: </label> 
            <input type="text" value={this.state.text} onChange={this.handleChange}/>
            <button onClick={this.handleClick}>Añadir</button>
            <div className="Listas">
            <ul>
                {
                    this.state.items.map(item =>(
                        <li key={item.id}>{item.text}</li>
                    ))
                }
            </ul>
            <br/>

            <ul>
                {
                    this.state.items2.map(item =>(
                        <li key={item.id}>{item.text}</li>
                    ))
                }
            </ul>
            <br/>
            </div>

            <p>Producto: {producto.ref}</p>
        </>
    }
}

export default ToDoApp;