import logo from './logo.svg';
import './App.css';
import Hellomessage from './componentes/Hellomsj';
import { personas } from './datos';
import Hellomessage2 from './componentes/Hellomsj2';
import ToDoApp from './componentes/ToDoApp';
import Datacontext from './componentes/DataContext';
import ClassTimer from './componentes/ClassTimer';
import { BrowserRouter as Router, Routes, Route, Link } from "react-router-dom";
import { Nav, Navbar, NavDropdown, Container } from "react-bootstrap"

function App() {

  let user1 = {
    nombre: "Johan", apellido: "Hernández"
  }

  let item1 = {
    ref: "ABC123", desc: "Item1", Q: 10
  }

  return (
    <>
      <Router>
        <Navbar bg="light" expand="lg">
          <Container>
            <Navbar.Brand href="home">Home</Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="me-auto">
                <Nav.Link as={Link} to="ToDoApp">ToDo</Nav.Link>
                <Nav.Link as={Link} to="Clock">Hora Actual</Nav.Link>
                <NavDropdown title="Menu" id="basic-nav-dropdown">
                  <NavDropdown.Item href="Saludo1">SALUDO 1</NavDropdown.Item>
                  <NavDropdown.Item href="Saludo2">SALUDO 2</NavDropdown.Item>
                  <NavDropdown.Divider />
                  <NavDropdown.Item href="Clock">Hora Actual</NavDropdown.Item>
                </NavDropdown>
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>

        <Datacontext.Provider value={item1}>
          <Routes>
            <Route path="ToDoApp" element={<ToDoApp user={user1} />} />
            <Route path="Clock" element={<ClassTimer />} />
            <Route path="Saludo1" element={<Hellomessage nombre={personas[0].nombre + " " + personas[0].apellido} />}/>
            <Route path="Saludo2" element={
              <>
                <Hellomessage2 user={user1} />
                <Hellomessage2 />
              </>
            }/>  
            <Route path="home" element={
              <div className="App">
                <header className="App-header">
                  <img src={logo} className="App-logo" alt="logo" />
                  <p>Hola Mundo Desde React</p>
                  <a className="App-link" href="https://reactjs.org" target="_blank" rel="noopener noreferrer">
                    Learn React
                  </a>
                </header>
              </div>
            } />
          </Routes>
        </Datacontext.Provider>
      </Router>
    </>
  );
}

export default App;
