import React from "react";
import { Datacontext } from "./DataContext";

class ClassTimer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            time: new Date(),
            day: new Date()
        };
    }

    componentDidMount() {

        this.interval = setInterval(() => {
            this.setState(() => ({
                time: new Date(),
                day: new Date()
            }));
        }, 1000);
    }

    componentWillUnmount() {
        console.log("Desmontado ClassTimer");
        clearInterval(this.interval);
    }

    render() {
        return (
            <>
                <h1>Reloj Digital </h1>
                <h2> son las {this.state.time.toLocaleTimeString()} del dia {this.state.day.toLocaleDateString()}</h2>
            </>
        );
    }
}

export default ClassTimer;