import React from "react";
import "./Hellomsj2.css";

class Hellomessage2 extends React.Component{
    render(){
        function formatNombre(user){
            return user.nombre + " " + user.apellido; 
        }

        function validarsaludo(user){
            if (user) {
                return <h3>Hello {formatNombre(user)} </h3>
            } else{
                return <h3>Usuario desconocido </h3>
            }

        }
        return <>
            <div className="Titulo"> {validarsaludo(this.props.user)} </div>
        </>
    }
}

export default Hellomessage2;
